* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

section {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: calc(100vh - 81px);
  overflow-x: hidden;

  @media screen and (max-width: 600px) {
    height: 100vh;
  }
}

section .bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

section .trees {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 100;
  pointer-events: none;
}

section .girl {
  position: absolute;
  scale: 0.65;
  animation: animateGirl 10s linear infinite;
}

@keyframes animateGirl {
  0% {
    transform: translateX(calc(100% + 100vw))
  }

  50% {
    transform: translateX(calc(-100% - 100vw))
  }

  50.01% {
    transform: translateX(calc(-100% - 100vw)) rotateY(180deg)
  }

  100% {
    transform: translateX(calc(100% + 100vw)) rotateY(180deg)
  }

}

.profile {
  min-height: 400px;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(15px);
  border-radius: 20px;
  width: 55%;
  display: flex;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.1);

  @media screen and(max-width: 1024px) {
    width: 70%;
  }

  @media screen and(max-width: 767px) {
    width: 85%;
  }

  @media screen and(max-width: 550px) {
    width: 92%;
  }

  @media screen and(max-width: 450px) {
    flex-direction: column;
  }

  .info {
    width: 40%;
    background-color: #eb746b;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;

    @media screen and(max-width: 450px) {
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
      border-bottom-left-radius: 0;
      width: 100%;
    }


    .infoContainer {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      padding: 20px 0;
      height: 100%;

      @media screen and(max-width: 450px) {
        padding: 10px 0;
      }

      .name,
      .address {
        padding: 5px 25px;
        background-color: #bf5a51;
        border-radius: 30px;
        margin-bottom: 0;
        color: #fff;
        font-size: 18px;
        font-weight: bold;
        font-family: 'Literata', serif;

        @media screen and(max-width: 450px) {
          display: none;
        }
      }

      .address {
        cursor: pointer;
      }

      .avatar {
        display: flex;
        align-items: center;

        .update {
          width: 100%;
          text-align: center;
        }

        .img {
          width: 150px;
          height: 150px;
          border-radius: 50%;
          object-fit: cover;

          @media screen and(max-width: 450px) {
            width: 100px;
            height: 100px;
          }

          img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
            object-fit: cover;
            border: 10px solid #cf645c;
          }

        }
      }

    }

  }

  .content {
    flex: 1;
    padding: 16px 20px;

    .details {
      text-align: center;
      font-size: 24px;
      font-weight: 600;
      color: #000;
      font-family: 'Literata', serif;

    }
  }
}

.update {
  text-decoration: underline;
  cursor: pointer;
}

.leaves {
  position: absolute;
  width: 100%;
  height: calc(100vh - 81px);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  pointer-events: none;
}

.leaves .set {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;

}

.leaves .set div {
  position: absolute;
  display: block;
}

.leaves .set div:nth-child(1) {
  left: 20%;
  animation: animate 20s linear infinite;
}

.leaves .set div:nth-child(2) {
  left: 50%;
  animation: animate 14s linear infinite;
}

.leaves .set div:nth-child(3) {
  left: 70%;
  animation: animate 12s linear infinite;
}

.leaves .set div:nth-child(4) {
  left: 5%;
  animation: animate 15s linear infinite;
}

.leaves .set div:nth-child(5) {
  left: 85%;
  animation: animate 18s linear infinite;
}

.leaves .set div:nth-child(6) {
  left: 90%;
  animation: animate 12s linear infinite;
}

.leaves .set div:nth-child(7) {
  left: 15%;
  animation: animate 14s linear infinite;
}

.leaves .set div:nth-child(8) {
  left: 60%;
  animation: animate 15s linear infinite;
}

@keyframes animate {
  0% {
    opacity: 0;
    top: -10%;
    transform: translateX(20px) rotate(0deg);
  }

  10% {
    opacity: 1;
  }

  20% {
    transform: translateX(-20px) rotate(45deg);
  }

  40% {
    transform: translateX(-20px) rotate(90deg);
  }

  60% {
    transform: translateX(20px) rotate(180deg);
  }

  80% {
    transform: translateX(-20px) rotate(45deg);
  }

  100% {
    top: 110%;
    transform: translateX(20px) rotate(225deg);
  }
}